"use client"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Edit2, Users } from "lucide-react"
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip"

interface Jury {
  id: number
  name: string
}

interface Soutenance {
  id: number
  date: string
  time: string
  location: string
  groupe: string
  juryMembers: Jury[] // List of jury members
  status: string
}

interface SoutenanceTableProps {
  soutenances?: Soutenance[] // Make the prop optional
}

const StatusBadge = ({ status }: { status: string }) => {
  const getStatusStyles = (status: string) => {
    switch (status.toLowerCase()) {
      case "planifi√©":
        return "bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-300"
      case "termin√©":
        return "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300"
      case "annul√©":
        return "bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300"
      default:
        return "bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-300"
    }
  }

  return (
    <Badge variant="outline" className={`${getStatusStyles(status)} font-medium`}>
      {status}
    </Badge>
  )
}

const JuryList = ({ juryMembers }: { juryMembers: Jury[] }) => {
  return (
    <TooltipProvider>
      <Tooltip>
        <TooltipTrigger asChild>
          <div className="flex items-center gap-2">
            <Users className="h-4 w-4 text-muted-foreground" />
            <span className="truncate max-w-[150px]">
              {juryMembers.map((jury) => jury.name).join(", ")}
            </span>
          </div>
        </TooltipTrigger>
        <TooltipContent>
          {juryMembers.map((jury) => jury.name).join(", ")}
        </TooltipContent>
      </Tooltip>
    </TooltipProvider>
  )
}

export default function SoutenanceTable({ soutenances = [] }: SoutenanceTableProps) {
  if (!soutenances || soutenances.length === 0) {
    return (
      <Card className="w-full p-6 text-center">
        <p className="text-muted-foreground">Aucune soutenance disponible</p>
      </Card>
    )
  }

  return (
    <Card className="w-full overflow-hidden border-t-0 rounded-t-none">
      <div className="relative w-full overflow-auto">
        <Table>
          <TableHeader className="bg-muted/50">
            <TableRow>
              <TableHead className="w-[180px] font-semibold">Date</TableHead>
              <TableHead className="w-[100px] font-semibold">Heure</TableHead>
              <TableHead className="w-[150px] font-semibold">Lieu</TableHead>
              <TableHead className="w-[120px] font-semibold">Groupe</TableHead>
              <TableHead className="w-[200px] font-semibold">Jury</TableHead>
              <TableHead className="w-[120px] font-semibold">Statut</TableHead>
              <TableHead className="w-[100px] font-semibold">Actions</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {soutenances.map((soutenance) => (
              <TableRow key={soutenance.id} className="transition-colors hover:bg-muted/50">
                <TableCell className="font-medium">
                  {new Date(soutenance.date).toLocaleDateString("fr-FR", {
                    day: "numeric",
                    month: "long",
                    year: "numeric",
                  })}
                </TableCell>
                <TableCell>{soutenance.time}</TableCell>
                <TableCell>{soutenance.location}</TableCell>
                <TableCell>
                  <Badge variant="secondary">{soutenance.groupe}</Badge>
                </TableCell>
                <TableCell>
                  <JuryList juryMembers={soutenance.juryMembers} />
                </TableCell>
                <TableCell>
                  <StatusBadge status={soutenance.status} />
                </TableCell>
                <TableCell>
                  <Button
                    variant="ghost"
                    size="icon"
                    className="hover:bg-muted"
                    onClick={() => console.log(`Edit soutenance ${soutenance.id}`)}
                  >
                    <Edit2 className="h-4 w-4" />
                    <span className="sr-only">Edit soutenance</span>
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
    </Card>
  )
}
